@import url('https://fonts.googleapis.com/css2?family=Anton&family=Montserrat:wght@400;600&display=swap');

$blue:#6383fe;

@mixin flex-column {
    display: flex;
    flex-direction: column;
}

@mixin sector {
    position: absolute;
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 200px;
    color: $blue;
    font-weight: 700;

    .video-sector__name {
        text-transform: uppercase;
    }

    .video-sector__number {
        font-size: 20px;
    }
    
    .video-sector__line{
        display: block;
        width:25px;
        height:2px;
        background-color:$blue;
    }
}

@mixin section-number{
    display:flex;
    justify-content: space-between;
    align-self: flex-start;
    min-width: 210px;
    transform:rotate(90deg);
    
        .section-number__line{
            position: relative;
            display: block;
            width:150px;
            border-top:1px solid rgba(255,255,255,.31);
            
            &::after{
                content: '';
                display: block;
                position: absolute;
                width:30px;
                height:3px;
                background-color:#fff;
                top:-8px;
                left:3px;
                
            }
        }
}




* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body {
    padding-top: 35px;
    font-size: 15px;
    font-family: 'Montserrat', sans-serif;
    background-color: #000;
    color: #fff;
}

h1 {
    font-weight: 400;
    font-size: 190px;
    font-family: 'Anton';
    text-transform: uppercase;
}

h2 {
    font-size: 170px;
    font-family: 'Anton';
    text-transform: uppercase;
    font-weight: 400;

    .title-bottom {
        font-size: 188px
    }
}

h3 {
    font-size: 20px;
    font-weight: 600px;
    text-transform: uppercase;
}

ul {
    list-style: none;
}

a {
    text-decoration: none;
    color: #fff;
}

p {
    color: rgba(255, 255, 255, 0.5);
}

.container {
    max-width: 1400px;
    margin: auto;
    padding: 0 35px;
}

header {
    display: flex;
    justify-content: space-between;
}

.header__navigation {
    display: none;
}

.agency {
    position: relative;
    display: flex;
    justify-content: flex-end;
    margin-top: 150px;
}

.agency__title-link {
    position: absolute;
    width: 60%;
    top: -30px;
    left: 0;
}

.title-top {
    z-index: -1;
}

.title-bottom{
    z-index: 1;
}

.agency__title {
    @include flex-column;
    margin: 0;

    .agency__title-top {
        color: #000;
        text-shadow: 0px 0px 4px #fff;
    }

    .agency__title-bottom {
        font-size: 230px;
        margin-top: -50px;
    }
}


.link {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 205px;
    height: 65px;
    margin-top: 50px;
    margin-left: 50px;
    padding: 0px 30px;
    background-color: #fff;
    color: #000;

    &::after {
        content: '';
        display: inline-block;
        background-color: white;
        border-right: 1px solid black;
        border-bottom: 1px solid black;
        padding: 5px;
        transform: rotate(-45deg);
    }
}


.agency__text-video {
    @include flex-column;
    align-items: flex-end;
    width: 70%;

    .agency__text {
        width: 325px;
        margin-bottom: 25px;
        text-align: left;
        line-height: 23px;
    }

    video {
        width: 100%;
        object-fit: cover;
    }
}

.trend {
    position: relative;
    display: flex;
    justify-content: space-between;
    margin-top: 500px;
}

.trend__title-link {
    position: absolute;
    width: 60%;
    top: -30px;
    left: 0;
}

.trend__title {
    @include flex-column;
    margin-top: -110px;

    .trend__title-top {
        color: black;
        text-shadow: 0px 0px 5px #fff;
    }

    .trend__title-bottom {
        color: $blue;
        margin-top: -30px;
    }
}

.trend__text-video {
    display: flex;
    justify-content: flex-end;

    .trend__video {
        position: relative;
        width: 75%;
        display: flex;
        justify-content: flex-end;
        margin-right: 30px;

        video {
            width: 70%;
            object-fit: cover;
        }

        .trend__video-sector {
            @include sector;
            right:-105px;
            top:40px;
        }
    }

    .trend__text-block {
        width: 25%;
        @include flex-column;
        justify-content: flex-end;
    }
}

.trend__section-number{
  @include section-number;
    
}
